<script>
import { mapActions, mapGetters } from "vuex";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //
export default {
  name: "reportSalesOutSee",
  data() {
    return {
      authority: {},
      sum: null,
      title: null,
      tableData: [],
      params: {}
    };
  },
  methods: {
    ...mapActions(["customerdetrptIndexACT", "printingStatus"]),
    // table 
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content"></p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            
          </span>
        </el-tooltip>
      );
    },
    goBack() {
      this.customerdetrptIndexACT({
        moduleName: "Index",
        params: this.customerdetrptSalesOutSeeGet.params
      });
    },
    lodopDe(name) {
      this.printingStatus(true);
      this.$set(this, "title", name);
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          outstock_id: this.params.outstock_id
        });
      });
    },
    // 
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "";
          return;
        }
        if (
          [
            // "wait_num",
            "real_num",
            "preferential_price",
            "outstock_price"
          ].includes(column.property)
        ) {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr) || 0;
          }, 0);
        }
        // if ("wait_num" == column.property) this.wait_num_total = sums[index];
        if ("real_num" == column.property) this.real_num_total = sums[index];
        if (["preferential_price"].includes(column.property)) {
          sums[index] = sums[index].toFixed(2);
        }
        if (["outstock_price"].includes(column.property)) {
          sums[index] = sums[index].toFixed(2);
        }
      });
      return sums;
    },
    auditOrUnaudit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          outstock_id: this.params.outstock_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg || res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },
  components: {
    lodopDelivery
  },
  computed: {
    ...mapGetters(["customerdetrptSalesOutSeeGet", "printingStatusGet"])
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axios({
      method: "get",
      url: this.$api.salesCheckOutstock,
      params: {
        outstock_id: this.customerdetrptSalesOutSeeGet.outstock_id,
        outstock_sn: this.customerdetrptSalesOutSeeGet.outstock_sn
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.params = res.data.data;
        this.tableData = res.data.data.goods_info;
      }
    });
  }
};
</script>

