<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportThreeoutstorageSee",
  data() {
    return {
      authority: {},
      sum: null,
      tableData: [],
      params: {}
    };
  },
  methods: {
    ...mapActions(["customerdetrptIndexACT"]),
    goBack() {
      this.customerdetrptIndexACT({
        moduleName: "Index"
      });
    },
    auditOrUnaudit(api) {
      //
      this.$axMethod[api]({
        outstock_id: this.params.outstock_id
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$message({
            message: data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 
    settlementPriceFormatter(scope){
      if(scope.row.business_type == ''){
        this.$set(this.tableData[scope.$index], 'settlement_price', !!(-scope.row.claim_price) ? -scope.row.claim_price : 0);
        return Number(scope.row.settlement_price).toFixed(2)
      }

      this.$set(
        this.tableData[scope.$index],
        'settlement_price',
        !!(scope.row.real_num *scope.row.goods_price) ? (scope.row.real_num* scope.row.goods_price) : 0
      )
      return Number(scope.row.settlement_price).toFixed(2)
    }
  },
  computed: {
    ...mapGetters(["customerdetrptThreeoutstorageSeeGet"])
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axios({
      method: "get",
      url: this.$api.infoThreeOutStorage,
      params: {
        outstock_id: this.customerdetrptThreeoutstorageSeeGet.outstock_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.params = res.data.data;
        this.tableData = res.data.data.goods_info;
      }
    });
  }
};
</script>


